import {reqGoodsList} from '../../util/request'

const state= {
    list:[],
    size:2,
    tatal:0,
    page:1
}
const mutations= {
    changeList(state,arr){
        state.list = arr
    },
    changeTotal(state,total){
        state.list = total
    },
    changePage(state,page){
        state.list = page
    }
}
const actions= {
    requsetGoodsList(context){
        reqGoodsList({page: 1, size: 2 }).then(res=>{
            if(res.data.code===200){
                const list = res.data.list
                context.commit('changeList',list)
            }
        })
    }
}
const getters= {
    list(state){
        return state.list
    },
    total(state){
        return state.total
    },
    page(state){
        return state.page
    }
}

export default{
    state,
    mutations,
    actions,
    getters,
    namespaced: true
}


